export async function select(
  options?: Parameters<typeof Editor.Dialog.select>[0],
  window?: Electron.BrowserWindow
): Promise<{ canceled: boolean; filePaths: string[] }> {
  const result = await Editor.Dialog.select(options, window);

  if (!result) {
    return { canceled: true, filePaths: [] };
  }

  if (result.length) {
    // Array, filePaths.
    return { canceled: false, filePaths: result };
  }

  return result;
}

export async function save(
  options?: Parameters<typeof Editor.Dialog.save>[0],
  window?: Electron.BrowserWindow
): Promise<{ canceled: boolean; filePath?: string }> {
  const result = await Editor.Dialog.save(options, window);
  if (!result) {
    return { canceled: true };
  }
  if (result.canceled === undefined) {
    return { canceled: false, filePath: result };
  }
  return result;
}

export async function info(
  message: string,
  options?: Parameters<typeof Editor.Dialog.info>[1],
  window?: Electron.BrowserWindow
): Promise<number> {
  const result = await Editor.Dialog.info(message, options, window);
  if (result.response !== undefined) {
    return result.response;
  } else {
    return result;
  }
}
